<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process157"></a>

<p><br></p>
<p><span class="rvts17">乞丐和狗（</span><span class="rvts50">A Man and His Dog</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:347,Y:114</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：乞丐（</span><span class="rvts20">Begger</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">进入贼窝，揭示骗局</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1123.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该任务需要和浮木广场上的乞丐说话。这个乞丐非常的势利眼，一旦你给他钱，他会点头哈腰。一旦你拒绝给他钱，他会给你翻白眼。</span>
<br><span class="rvts21">使用</span><span class="rvts20"></span><span class="rvts21">动物伙伴</span><span class="rvts20"></span><span class="rvts21">技能，与他的狗对话。会发现这只狗病的很厉害，并且受到乞丐的挟持被迫要饭。这只狗其实是有主人的。可以把这只狗放了。</span>
<br><span class="rvts21">还可以羞辱乞丐的不光彩行为，并为他这种好吃懒做的生活方式感到愤怒。此时乞丐会把之前我们给他的钱全部退回来，并赶走我们。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1124.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">之后我们可以在石碑花园找到这只狗，然而，这只狗为什么会出现在这里，还不得而知。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



